<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag v-else-if="scope.row.level === '1'" type="success"></el-tag>
            <el-tag v-else type="warning"></el-tag>
          </template>
        </el-table-column>
        
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightsList: [],
    };
  },
  created() {
    this.getRightsList();
  },
  methods: {
    async getRightsList() {
      const res = await this.$axios.get("rights/list");
      this.rightsList = res.data.data;
      console.log(this.rightsList);
    },
  },
};
</script>

