<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-button type="primary" @click="showAddCateDialog"
          ></el-button
        >
      </el-row>
      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightblue"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            type="success"
            ></el-tag
          >
          <el-tag size="mini" v-else type="warning"></el-tag>
        </template>
        <template slot="option" slot-scope="scope" >
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="editCate(scope.row)"
            ></el-button
          >
          <el-button size="mini" type="danger" icon="el-icon-delete"
            ></el-button
          >
        </template>
      </tree-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClose"
    >
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChange"
            change-on-select
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form
        :model="editCateForm"
        :rules="editCateFormRules"
        ref="editCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editCateForm.goods_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="putCate"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: [],
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      total: 0,
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {
          label: "",
          type: "template",
          template: "isok",
        },
        {
          label: "",
          type: "template",
          template: "order",
        },
        {
          label: "",
          type: "template",
          template: "option",
        },
      ],
      addCateDialogVisible: false,
      addCateForm: {
        cat_name: "",
        cat_pid: 0,
        // 
        cat_level: 0,
      },
      addCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      parentCateList: [],
      // 
      cascaderProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      // id
      selectedKeys: [],
      editDialogVisible: false,
      editCateForm: {
        goods_name: "",
      },
      editCateFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      cateById:{}
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const res = await this.$axios.get("categories", {
        params: this.queryInfo,
      });
      console.log(res.data)
      this.cateList = res.data.data.result;
      this.total = res.data.data.total;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getCateList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getCateList();
    },
    showAddCateDialog() {
      // 
      this.getParentCateList();
      this.addCateDialogVisible = true;
    },
    async getParentCateList() {
      const res = await this.$axios.get("categories", { params: { type: 2 } });
      this.parentCateList = res.data.data;
    },
    // 
    parentCateChange() {
      // console.log(this.selectedKeys)
      if (this.selectedKeys.length > 0) {
        // id
        this.addCateForm.cat_pid =
          this.selectedKeys[this.selectedKeys.length - 1];
        // 
        this.addCateForm.cat_level = this.selectedKeys.length;
      } else {
        this.addCateForm.cat_pid = 0;
        // 
        this.addCateForm.cat_level = 0;
      }
    },
    addCate() {
      this.$refs.addCateFormRef.validate(async (boolean) => {
        if (!boolean) return;
        const res = await this.$axios.post("categories", this.addCateForm);
        if (res.data.meta.status !== 200) {
          this.$message.error("");
        }
        this.$message.success("");
        this.getCateList();
        this.addCateDialogVisible = false;
      });
    },
    // ,
    addCateDialogClose() {
      this.$refs.addCateFormRef.resetFields();
      this.selectedKeys = [];
      this.addCateForm.cat_lelve = 0;
      this.addCateForm.cat_pid = 0;
    },
    async editCate(row) {
      const resById = await this.$axios.get(`goods/${row.cat_id}`)
      // console.log(resById.data.data)
      this.cateById = resById.data.data
      this.editCateForm.goods_name = row.cat_name
      this.editDialogVisible = true;
    },
    async putCate(){
      const res = await this.$axios.put(`goods/${this.cateById.goods_id}`,{id: this.cateById.goods_id,goods_name: this.editCateForm.goods_name,goods_price:this.cateById.goods_price,goods_number: this.cateById.goods_number,goods_weight:this.cateById.goods_weight})
      console.log(res.data)
      if(res.data.meta.status !== 200){
        this.$message.error('')
      }
      this.editDialogVisible = false
    }
  },
};
</script>

<style>
.zk-table {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
.el-scrollbar .el-scrollbar__wrap {
  height: 200px;
}
</style>