<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-alert
        show-icon
        title=""
        type="warning"
        :closable="false"
      >
      </el-alert>

      <el-row class="cate-option">
        <span></span>
        <el-cascader
          expand-trigger="hover"
          v-model="selectedCateKeys"
          :options="cateList"
          :props="cateProps"
          @change="handleChange"
        ></el-cascader>
      </el-row>

      <!-- tabs -->
      <el-tabs v-model="activeName" @tab-click="handleTabsClick">
        <el-tab-pane label="" name="many">
          <el-button
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
            @click="addDialogVisible = true"
            ></el-button
          >
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                @close='tagClose(i,scope.row)'
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="deleteParam(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
            @click="addDialogVisible = true"
            ></el-button
          >
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  >{{ item }}</el-tag
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="deleteParam(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + dialogTitle"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item :label="dialogTitle" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParam"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="'' + dialogTitle"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editForm"
        :rules="addFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item :label="dialogTitle" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParam"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: [],
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      selectedCateKeys: [],
      activeName: "many",
      //   
      manyTableData: [],
      //   
      onlyTableData: [],
      addDialogVisible: false,
      addForm: {
        attr_name: "",
      },
      addFormRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      editDialogVisible: false,
      editForm: {
        attr_name: "",
      },
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const res = await this.$axios.get("categories");
      // console.log(res.data)
      this.cateList = res.data.data;
    },
    handleChange() {
      this.getParamsData();
    },
    handleTabsClick() {
      console.log(this.activeName);
      this.getParamsData();
    },
    // 
    async getParamsData() {
      //   console.log(this.selectedCateKeys);
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = [];
        this.manyTableData = []
        this.onlyTableData = []
        return;
      }
      // 
      //  id
      const res = await this.$axios.get(
        `categories/${this.cateId}/attributes`,
        { params: { sel: this.activeName } }
      );
      // console.log(res.data.data);
      res.data.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        // 
        item.inputVisible = false;
        // 
        item.inputValue = "";
      });
      if (this.activeName === "many") {
        this.manyTableData = res.data.data;
      } else {
        this.onlyTableData = res.data.data;
      }
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields();
    },
    addParam() {
      this.$refs.addFormRef.validate(async (boolean) => {
        if (!boolean) return;
        const res = await this.$axios.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName,
          }
        );
        if (res.data.meta.status !== 201) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.addDialogVisible = false;
        this.getParamsData();
      });
    },
    async showEditDialog(attr_id) {
      const res = await this.$axios.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        {
          params: { attr_sel: this.activeName },
        }
      );
      this.editForm = res.data.data;
      this.editDialogVisible = true;
    },
    editDialogClose() {
      this.$refs.editFormRef.resetFields();
    },
    editParam() {
      this.$refs.editFormRef.validate(async (boolean) => {
        if (!boolean) return;
        const res = await this.$axios.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName,
          }
        );
        if (res.data.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.getParamsData();
        this.editDialogVisible = false;
      });
    },
    async deleteParam(attr_id) {
      const confirmRes = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmRes !== "confirm") {
        return this.$message.info("");
      }
      await this.$axios.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      );

      this.$message.success("");
      this.getParamsData();
    },
    //   enter 
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = "";
        row.inputVisible = false;
        return;
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.saveVals(row)
    },
    async saveVals(row){
      // 
      await this.$axios.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      this.$message.success('')
    },
    showInput(row) {
      row.inputVisible = true;
      // 
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 
    tagClose(i,row){
      row.attr_vals.splice(i,1)
      this.saveVals(row)
    }
  },
  computed: {
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true;
      }
      return false;
    },
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2];
      }
      return null;
    },
    // 
    dialogTitle() {
      if (this.activeName === "many") {
        return "";
      } else {
        return "";
      }
    },
  },
};
</script>

<style lang='less'>
.cate-option {
  margin: 15px 0;
  display: flex;
  span {
    width: 132px;
    line-height: 41px;
  }
}
.el-cascader .el-input .el-input__inner {
  width: auto;
}
.el-tag {
  margin: 10px;
}
.el-input--small .el-input__inner {
  width: 120px;
}
</style>