<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>

      <el-table :data="orderList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === 1" type="success"
              ></el-tag
            >
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dataFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showEditAddressDialog"
            ></el-button>
            <el-button
              type="success"
              size="mini"
              icon="el-icon-location"
              @click="showLogistics"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <el-dialog
        title=""
        :visible.sync="addressVisible"
        width="50%"
        @close="addressDialogClose"
      >
        <el-form
          :model="addressForm"
          :rules="addressFormRules"
          ref="addressFormRef"
          label-width="100px"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              v-model="addressForm.address1"
              :options="cityData"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="addressForm.addres2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addressVisible = false"> </el-button>
          <el-button type="primary" @click="addressVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="logisticsVisible" width="50%">
        <!--  -->
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in logisticsInfo"
            :key="index"
            :timestamp="activity.time"
          >
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="logisticsVisible = false"> </el-button>
          <el-button type="primary" @click="logisticsVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityData from "./city_data2017_element";
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
      orderList: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        address2: "",
      },
      addressFormRules: {
        address1: [
          { required: true, message: "/", trigger: "blur" },
        ],
        address2: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      cityData,
      logisticsVisible: false,
      logisticsInfo: [],
    };
  },
  created() {
    this.getOrderList();
  },
  methods: {
    async getOrderList() {
      const res = await this.$axios.get("orders", { params: this.queryInfo });
      this.total = res.data.data.total;
      this.orderList = res.data.data.goods;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrderList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOrderList();
    },
    showEditAddressDialog() {
      this.addressVisible = true;
    },
    addressDialogClose() {
      this.$refs.addressFormRef.resetFields();
    },
    async showLogistics() {
      //   const res = await this.$axios.get("/kuaidi/804909574412544580");
      //   console.log(res.data)
      // //   this.logisticsInfo = res.data.data;
      this.logisticsVisible = true;
    },
  },
};
</script>

<style>
.el-card {
  padding-bottom: 40px;
}
.el-cascader .el-input .el-input__inner {
  width: 100%;
}
</style>