<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-button type="primary" @click="editDialogVisible = true"
          ></el-button
        >
      </el-row>

      <el-table :data="rolesList" border stripe="">
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="[
                'border-bottom',
                l1 === 0 ? 'border-top' : '',
                'vcenter',
              ]"
              v-for="(item, l1) in scope.row.children"
              :key="item.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRight(scope.row, item.id)">{{
                  item.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="[l2 === 0 ? '' : 'border-top', 'vcenter']"
                  v-for="(item2, l2) in item.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRight(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      closable
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      @close="removeRight(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
              ></el-button
            >
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteRole(scope.row.id)"
              ></el-button
            >
            <!--  -->
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="giveRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editRoleForm"
        :rules="editFormRules"
        ref="editRoleFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="roleName" label-width="80px">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc" label-width="80px">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRolesEvent"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editRoleForm"
        :rules="editFormRules"
        ref="editRoleFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="roleName" label-width="80px">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc" label-width="80px">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRolesEvent"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="deCheckedKeys"
        ref="treeRef"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="distributeRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      editDialogVisible: false,
      addRole: {
        roleName: "",
        roleDesc: "",
      },
      editRoleForm: {},
      editFormRules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "2~8",
            trigger: "blur",
          },
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      setRightDialogVisible: false,
      rightsList: [],
      // 
      treeProps: {
        label: "authName",
        children: "children",
      },
      // id
      deCheckedKeys: [],
    //   id
      roleId: ''
    };
  },
  created() {
    this.getRolesList();
  },
  methods: {
    async getRolesList() {
      const res = await this.$axios.get("roles");
      this.rolesList = res.data.data;
    },
    editDialogClose() {
      this.$refs.editRoleFormRef.resetFields();
    },
    // 
    async showEditDialog(id) {
      const res = await this.$axios.get("roles/" + id);
      this.editRoleForm = res.data.data;
      console.log(this.editRoleForm);
      this.editDialogVisible = true;
    },
    addRoleEvent() {
      this.$refs.editRoleFormRef.validate((boolean) => {
        if (!boolean) return;
        const res = this.$axios.post("roles", {
          roleName: this.addRole.roleName,
          roleDesc: this.addRole.roleDesc,
        });
        this.$message.success("");
        // 
        this.editDialogVisible = false;
        this.getRolesList();
      });
    },
    async editRolesEvent() {
      this.$refs.editRoleFormRef.validate(async (boolean) => {
        if (!boolean) return;
        const res = await this.$axios.put("roles/" + this.editRoleForm.roleId, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc,
        });
        console.log(this.editRoleForm);
        if (res.data.meta.status !== 200) {
          this.$message.info("");
        } else {
          console.log(res.data);
          // 
          this.editDialogVisible = false;
          this.getRolesList();
          this.$message.success("");
        }
      });
    },
    // id
    async deleteRole(id) {
      const confirmRes = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmRes !== "confirm") {
        return this.$message.info("");
      }
      await this.$axios.delete("roles/" + id);
      this.$message.success("");
      this.getRolesList();
    },
    // 
    async removeRight(role, rightId) {
      const confirmRes = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      if (confirmRes !== "confirm") {
        return this.$message.info("");
      }

      const res = await this.$axios.delete(
        `roles/${role.id}/rights/${rightId}`
      );
      if (res.data.meta.status !== 200) {
        return this.$message.error("");
      }
      //   this.getRolesList();
      role.children = res.data.data;
    },
    // 
    async giveRightDialog(role) {
        this.roleId = role.id
      // 
      const res = await this.$axios.get("rights/tree");
      this.rightsList = res.data.data;
      //   id
      this.getAllRightsId(role, this.deCheckedKeys);
      this.setRightDialogVisible = true;
    },
    // id
    getAllRightsId(node, arr) {
      console.log(node);
      // 
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => this.getAllRightsId(item, arr));
    },
    // 
    setRightDialogClosed() {
      // id deCheckedKeys
      this.deCheckedKeys = [];
    },
    async distributeRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
    //   console.log(keys)
        const idStr = keys.join(',')
        const res = await this.$axios.post(`roles/${this.roleId}/rights`,{rids: idStr})
        this.$message.success('')
        this.getRolesList()
        this.setRightDialogVisible = false
    },
  },
};
</script>

<style scoped>
.el-tag {
  margin: 7px;
}
.border-top {
  border-top: 1px solid #eee;
}
.border-bottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>