<template>
  <div class="login">
    <div class="box">
      <el-form
        :model="ruleForm"
        :rules="rules"
        hide-required-asterisk
        ref="ruleForm"
        label-width="100px"
      >
        <el-form-item prop="username">
          <el-input
            prefix-icon="el-icon-user"
            placeholder=""
            style="width: 260px"
            autocomplete="off"
            v-model="ruleForm.username"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            prefix-icon="el-icon-unlock"
            placeholder=" token"
            style="width: 260px"
            autocomplete="off"
            v-model="ruleForm.password"
            type="password"
          ></el-input>
        </el-form-item>
        <div class="btns">
          <el-form-item>
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="reset"></el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    async login () {
      const username = this.ruleForm.username
      const password = this.ruleForm.password
      const res = await this.$axios.post('login', { username, password })
      // console.log(res)
      if (res.data.meta.status === 200) {
        console.log('')
        window.sessionStorage.setItem('token', res.data.data.token)
        this.$router.push('/home')
      } else {
        console.log('')
      }
    },
    reset() {
      const { ruleForm } = this;
      ruleForm.username = "";
      ruleForm.password = "";
    },
  },
};
</script>

<style lang='less'>
.login {
  width: 100vw;
  height: 100vh;
  background-color: lightblue;
  .box {
    width: 474px;
    // height: 210px;
    padding: 50px 0 20px 5px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 4px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -237px;
    margin-top: -105px;
    .el-form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .el-input {
        font-size: 16px;
      }
      .btns {
        margin-top: 30px;
        margin-left: 55px;
      }
    }
  }
}
</style>