<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
            clearable
            @clear="getUserList"
            v-model="userInfo.query"
            placeholder=""
          >
            <el-button
              slot="append"
              @click="getUserList"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>

      <el-table :data="userList" stripe border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="changeState(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteUser(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                @click="distributeRole(scope.row)"
                size="mini"
                type="warning"
                icon="el-icon-setting"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="userInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="userInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      @close="dialogClose"
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <el-form
        :model="addUser"
        :rules="addUserRules"
        ref="addUserRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUser.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUser.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUser.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addUser.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUserEvent"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserEvent"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      width="50%"
      @close='setRoleDialogClose'
    >
      <div>
        <p>{{ user.username }}</p>
        <p>{{ user.role_name }}</p>
        <p>
          
          <el-select v-model="selectRole" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRole"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var checkEmail = (rule, value, callback) => {
      const emailReg = /^([a-zA-Z0-9])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (emailReg.test(value)) {
        return callback();
      }
      callback(new Error(""));
    };
    var checkMobile = (rule, value, callback) => {
      const mobileReg =
        /^(0|86|17961)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (mobileReg.test(value)) {
        return callback();
      }
      callback(new Error(""));
    };
    return {
      userInfo: {
        query: "",
        // 
        pagenum: 1,
        pagesize: 2,
      },
      userList: [],
      total: 0,
      addDialogVisible: false,
      addUser: {
        username: "",
        password: "",
        email: "",
        mobile: "",
        setRoleDialogVisible: false,
      },
      addUserRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur", trigger: "blur" },
        ],
      },
      editFormRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur", trigger: "blur" },
        ],
      },
      editDialogVisible: false,
      // 
      editForm: {},
      setRoleDialogVisible: false,
      // 
      user: {},
      rolesList: {},
      selectRole: ''
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const res = await this.$axios.get("users", {
        params: this.userInfo,
      });
      this.userList = res.data.data.users;
      this.total = res.data.data.total;
      // console.log(res.data)
      //   console.log(this.userInfo);
      //   console.log(this.total);
    },
    handleSizeChange(newSize) {
      //   console.log(newSize);
      this.userInfo.pagesize = newSize;
      this.getUserList();
    },
    handleCurrentChange(newPage) {
      //   console.log(newPage);
      this.userInfo.pagenum = newPage;
      this.getUserList();
    },
    async changeState(userInfo) {
      // console.log(userInfo)
      const res = await this.$axios.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      );
      console.log(res.data);
      this.$message.success("");
    },
    // 
    dialogClose() {
      this.$refs.addUserRef.resetFields();
    },
    addUserEvent() {
      this.$refs.addUserRef.validate((boolean) => {
        if (!boolean) return;
        const res = this.$axios.post("users", this.addUser);
        this.$message.success("");
        // 
        this.addDialogVisible = false;
        this.getUserList();
      });
    },
    // 
    async showEditDialog(id) {
      const res = await this.$axios.get("users/" + id);
      this.editForm = res.data.data;
      this.editDialogVisible = true;
    },
    editDialogClose() {
      this.$refs.editFormRef.resetFields();
    },
    editUserEvent() {
      this.$refs.editFormRef.validate(async (boolean) => {
        if (!boolean) return;
        await this.$axios.put("users/" + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile,
        });
        // 
        this.editDialogVisible = false;
        this.getUserList();
        this.$message.success("");
      });
    },
    // id
    async deleteUser(id) {
      const confirmRes = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmRes !== "confirm") {
        return this.$message.info("");
      }
      await this.$axios.delete("users/" + id);
      this.$message.success("");
      this.getUserList();
    },
    async distributeRole(userInfo) {
      this.user = userInfo;
      const res = await this.$axios.get("roles");
      this.rolesList = res.data.data;
      this.setRoleDialogVisible = true;
    },
    async saveRole(){
      if(!this.selectRole){
        return this.$message.error('')
      }
      const res = await this.$axios.put(`users/${this.user.id}/role`,{
        rid: this.selectRole
      })
      if(res.data.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    setRoleDialogClose(){
      this.selectRole = ''
      this.user = ''
    }
  },
};
</script>

<style lang='less' scoped>
.el-main .el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
}
section.el-container {
  min-height: 100vh;
}
</style>