<template>
  <div class="home">
    <el-container>
      <el-header>
        <Header />
      </el-header>
      <el-container style="height: 100vh">
        <el-aside :width="isCollapse ? '64px' : '200px'" style="background-color: rgb(238, 241, 246)">
          <div class="button" @click="buttonCollapse">|||</div>
          <el-menu
            background-color="#333744"
            text-color="#fff"
            active-text-color="lightblue"
            :unique-opened="true"
            :collapse="isCollapse"
            :collapse-transition='false'
            :router="true"
            :default-active="activePath"
          >
            <el-submenu
              :index="item.id + ''"
              v-for="item in menuList"
              :key="item.id"
            >
              <template slot="title"
                ><i :class="icons[item.id]"></i
                ><span>{{ item.authName }}</span></template
              >
              <el-menu-item
                :index="'/' + subItem.path"
                v-for="subItem in item.children"
                :key="subItem.id"
                @click="saveState('/' + subItem.path)"
              >
                <template slot="title"
                  ><i class="el-icon-menu"></i
                  ><span>{{ subItem.authName }}</span></template
                >
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-container>
          <el-main>
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from "./Header.vue";
export default {
  components: { Header },
  data() {
    return {
      menuList: [],
      icons: {
        125: "iconfont icon-users",
        103: "iconfont icon-component",
        101: "iconfont icon-24gf-bag",
        102: "iconfont icon-dingdan",
        145: "iconfont icon-diannao-shuju",
      },
      isCollapse: false,
      activePath: ''
    };
  },
  created() {
    this.getMenuList();
    this.activePath = window.sessionStorage.getItem("activePath")
  },
  methods: {
    async getMenuList() {
      const { data: res } = await this.$axios.get("menus");
      this.menuList = res.data;
      // console.log(res);
    },
    // 
    buttonCollapse(){
      this.isCollapse = !this.isCollapse
    },
    saveState(activePath){
      window.sessionStorage.setItem('activePath',activePath)
      this.activePath = activePath
    }
  },
};
</script>

<style lang='less' scoped>
.el-aside {
  .el-menu {
    width: 200px;
    height: 96.5%;
    border-right: none;
  }
  .button {
    background-color: #4a5064;
    font-size: 10px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    letter-spacing: 0.2em;
    cursor: pointer;
  }
}
.home {
  overflow: hidden;
}
.home .el-header {
  padding: 0;
}
.el-container{
  max-height: 100vh;
}
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}
.iconfont {
  margin-right: 10px;
}
</style>