<template>
  <div class="header">
    <div class="left">
      <img src="https://www.escook.cn/vue-shop/img/heima.b5a855ee.png" alt="" />
      <div></div>
    </div>
    <div class="right">
      <el-button type="info" @click="loginOut"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    loginOut() {
      if (this.$route.path !== "/login") {
        this.$confirm("?", "", {
          confirmButtonText: "",
        })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {});
      }
      sessionStorage.removeItem("token");
    },
  },
};
</script>

<style lang='less'>
.header {
  padding: 0 20px;
  background-color: #333744;
  display: flex;
  justify-content: space-between;
  .left {
    display: flex;
    img,
    div {
      color: #fff;
    }
    div {
      font-size: 22px;
      margin-left: 20px;
      line-height: 56px;
    }
  }
  .right {
    align-items: center;
  }
  
}
.el-header{
  padding: 0;
}
</style>